export const getOption = (name) => {
    return new Promise((resolve, reject) => {
        apiFetch({
            path: `/jkit/v1/getOption?name=${encodeURIComponent(name)}`,
            method: 'GET'
        }).then(response => {
            resolve(response);
        }).catch(error => {
            reject(error);
        });
    });
};

