export const getConfig = (demo) => {
    return new Promise((resolve, reject) => {
        apiFetch({
            path: 'jkit/v1/demos/getConfig',
            method: 'POST',
            data: {
                nonce: '',
                demo: demo,
                domain: window.location.hostname,
                code: '',
            }
        }).then(response => {
            resolve(response);
        }).catch(error => {
            reject(error);
        });
    });
};

