const dataPaging = (data, paging, perPage) => {
    if (perPage) {
        let startIndex = perPage * (paging - 1);
        let result = data.slice(startIndex, startIndex + perPage);
        let totalPage = Math.ceil(data.length / perPage);

        return {
            current: paging,
            data: result,
            total: totalPage,
        };
    } else {
        return {
            current: 1,
            data: data,
            total: 1,
        };
    }
};

const demoFilter = (demoData, filter, selectedCategories) => {
    const { keyword, like, imported } = filter;

    let filteredData = demoData
        .filter(demo => {
            if (!!selectedCategories.length && !selectedCategories.includes('all')) {
                const slugs = demo.data.categories.map(cat => cat.slug);
                return slugs.some(slug => selectedCategories.includes(slug));
            }

            return true;
        })
        .filter((demo) => {
            const { data, like: demoLike, imported: demoImport } = demo;

            if (like) {
                if (false === demoLike) {
                    return false;
                }
            }

            if (imported) {
                if (false === demoImport) {
                    return false;
                }
            }

            if (keyword.length) {
                const { title } = data;
                const wordLowercase = title.toLowerCase();
                return keyword.some(k => wordLowercase.includes(k.toLowerCase()));
            }

            return true;
        });


    return filteredData;
};

const filterDemo = (demoData, filter, selectedCategories, perPage) => {
    const { paging } = filter;

    const data = demoFilter(demoData, filter, selectedCategories).map((demos) => {
        const { id, data, like, imported } = demos;
        const {
            categories,
            cover,
            demo,
            file,
            id: demo_id,
            newFlag,
            pro,
            requirements,
            slug,
            title,
            wordpress_req,
            demo_description,
            order
        } = data;

        return {
            id,
            slug,
            title,
            cover,
            like,
            demo,
            requirements,
            imported,
            newFlag,
            categories,
            file,
            demo_id,
            pro,
            wordpress_req,
            demo_description,
            order
        };
    });
    data.sort(function (a, b) {

        // show imported demo first
        if (a.imported && !b.imported) return -1;
        if (!a.imported && b.imported) return 1;

        // sorting by order field
        const orderA = a.order ?? 0;
        const orderB = b.order ?? 0;
        if (orderA === 0 && orderB !== 0) return 1;
        if (orderA !== 0 && orderB === 0) return -1;

        return orderA - orderB;
    });

    return dataPaging(data, paging, perPage);
};

export const mapId = (data) => {
    const result = {};

    data.map((item) => {
        result[item.id] = item;
    });

    return result;
};

const DemoContentList = (props) => {
    const { demoData, content, dashboardData, setContent, setSnackbar, importab, searchTerm, selectedCategories } = props;
    const data = dashboardData.demoContentData;

    useEffect(() => {
        let demos = demoData;



        const result = filterDemo(demos, data, selectedCategories, 12);
        const { data: newData, total, current } = result;

        setContent(prevState => {
            const { data: oldData } = prevState;
            const theData = data.paging === 1 ? newData : [
                ...oldData,
                ...newData.filter(
                    newItem => !oldData.some(oldItem => oldItem.id === newItem.id)
                )
            ];

            return {
                data: theData,
                total,
                current
            };
        });
    }, [demoData, data, searchTerm, selectedCategories]);

    return <>
        <div className="f-demo-demo-inner">
            {'wizard' !== props.origin && <DemoNavigation importab={importab} setImportab={props.setImportab} search={props.search} setSearch={props.setSearch} />}
            <LayoutContentData
                current={content.current}
                data={content.data}
                total={content.total}
                setSnackbar={setSnackbar}
                importab={importab}
                searchTerm={searchTerm}
                selectedCategories={selectedCategories}
                {...props}
            />
        </div>
    </>;
};

