const Paging = (props) => {
    const { current, total, scroller } = props;
    const scrollListener = event => {
        let totalHeight = 20;

        Array.prototype.slice.call(event.target.children).map(child => {
            totalHeight += child.offsetHeight;
        });

        let scrollTop = event.target.scrollTop;
        let wrapperHeight = event.target.offsetHeight;
        let totalScroll = scrollTop + wrapperHeight;

        if (current < total && totalScroll + 500 > totalHeight) {
            dispatch('jkit/dashboard').setPaging(current + 1);
        }
    };

    useEffect(() => {
        if (scroller) {
            const debounceOperation = throttle(scrollListener, 100);

            scroller?.current?.addEventListener('scroll', debounceOperation);

            return () => {
                scroller?.current?.removeEventListener('scroll', debounceOperation);
            };
        }
    });

    return current < total ? <div className="f-demo-paging">
        <div className={classNames('f-demo-paging-button')} onClick={() => {
            dispatch('jkit/dashboard').setPaging(current + 1);
        }}>
            <span>{__('Load More', 'jkit')}</span>
        </div>
    </div> : null;
};

export const LayoutContentData = (props) => {
    const { data, current, total, setSnackbar, importab, searchTerm, selectedCategories } = props;
    //changePaging is sipatch page
    if (data !== undefined) {
        const datas = data.filter(demo => demo !== undefined);
        return datas.length === 0 ? <div className="empty-content">
            <div className="empty-svg">
                <EmptySvg />
            </div>
            <h3>{__('Empty Result', 'jkit')}</h3>
        </div> : <>
            <DemoItem
                data={data}
                setSnackbar={setSnackbar}
                importab={importab}
                searchTerm={searchTerm}
                selectedCategories={selectedCategories}
                {...props}
            />
            <Paging current={current} total={total} />
        </>;
    } else {
        return null;
    }
};

const preprocessText = (text) => {
    const stopwords = [
        'for', 'and', 'the', 'a', 'an', 'to', 'of', 'in', 'on', 'at', 'with',
        'dan', 'yang', 'di', 'ke', 'dari', 'untuk'
    ];

    return text
        .toLowerCase()
        .replace(/[^a-z0-9\u00C0-\u017F\s]/gi, '')
        .split(/\s+/)
        .filter(word => word && !stopwords.includes(word))
        .join(' ');
}

